import Xhead  from "./Section/head.vue";
import MainLeftjison from "@/assets/json/MainLeft.json"; //
import XmainLeft  from "./Section/mainLeft.vue"; //
import XServer  from "./Member/Server.vue"; //
import XServerAuditing  from "./Member/ServerAuditing.vue"; //
import XformTable  from "./Section/formTable.vue"; //Table
import XMemberList  from "./Member/MemberList.vue"; //
import XActivNotice  from "./Member/ActivNotice.vue"; //
import XActivEmail  from "./Member/ActivEmail.vue"; //
import XActivNoticeToexamine  from "./Member/ActivNoticeToexamine.vue"; //
import XActivEmailToexamine  from "./Member/ActivEmailToexamine.vue"; //
import XNinBlacklist  from "./Member/NinBlacklist.vue"; //
import XNinBlacklistIp  from "./Member/NinBlacklistIp.vue"; //IP
import XWhitelist  from "./Member/Whitelist.vue"; //
import XAdmin from "./Member/Admin.vue" //---
import XAdminJurisdiction from "./Member/AdminJurisdiction.vue" //---
import XEchartsLine  from "./Section/EchartsLine.vue"; //Echarts


export default {
  name:"maina",
  components:{
      Xhead,
      XmainLeft,
      XServer,
      XServerAuditing,
      XformTable,
      XEchartsLine,
      XMemberList,
      XActivNotice,
      XActivEmail,
      XActivNoticeToexamine,
      XActivEmailToexamine,
      XNinBlacklist,
      XNinBlacklistIp,
      XWhitelist,
      XAdmin,
      XAdminJurisdiction
    },
  data () {
    return {
        RightActive:null,
        Listactive:null,
        screenHeight: document.documentElement.clientHeight - 90,//
        date: new Date(),
        getMonth:null ,// 
        getDay:null ,// 
        getSeconds:null,
        Navigation:null //
     
    }
  },
  mounted() {
      var _this = this;
      window.onresize = function(){ // 
        _this.screenHeight = document.documentElement.clientHeight -90
      };
      this.timer = setInterval(function() {
                let MYtime = new Date();//date    
                let arr=["","","","","","",""]
                _this.getMonth = (MYtime.getMonth() + 1 ) > 9 ? (MYtime.getMonth() + 1):'0' + ( MYtime.getMonth() + 1 )
                _this.getSeconds = MYtime.getSeconds()  > 9 ? MYtime.getSeconds() :'0' + MYtime.getSeconds()  
                _this.getDay =  arr[parseInt( MYtime.getDay() )] 
                _this.date  =  MYtime
      },1000); 
  },    
  created(){   
      var _this = this;
      let state =  this.$storage.getSession('state')
      if(state != '' && state != undefined && state != null){
          _this.RightActive =  state.RightActive
          _this.Listactive =  state.Listactive
      }  
      let MainJison = MainLeftjison 
      let km = MainLeftjison[_this.RightActive].name
      let kn = MainLeftjison[_this.RightActive].type[ _this.Listactive].name
      this.Navigation = ` ${km} => ${kn} `
     
  }, 
  methods: {
     animation(item,index){
        this.RightActive = item.RightActive
        this.Listactive = 0 
        this.$storage.setSession("state", {RightActive:this.RightActive,Listactive:this.Listactive}); 
        let km = MainLeftjison[this.RightActive].name
        let kn = MainLeftjison[this.RightActive].type[this.Listactive].name
        this.Navigation = ` ${km} => ${kn} `
    },
     listanim(Listactive,index){ 
       
        this.Listactive = Listactive;
        this.$storage.setSession("state", {RightActive:this.RightActive,Listactive:this.Listactive}); 
        let km = MainLeftjison[this.RightActive].name
        let kn = MainLeftjison[this.RightActive].type[this.Listactive].name
        this.Navigation = ` ${km} => ${kn} `
    }
  }
}
