import { Switch ,Dialog } from 'vant';

import Tablejison from "@/assets/json/Table.json";
import XTime  from "@/components/Section/Time.vue";
export default {
  name: "Server",
  components:{Switch,Dialog,XTime},
  data() {  
    return {
      TableAcitve: false,
      screenHeight:null,
      Tbody: ["", "", "", "",  "","()","","",""],
      Table: [],
      Tablelist: [],
      page: 1, //
      pages: null, //
      conter: 10, //
      Total: null,//
      Selection:[] ,//
      ServeName:'', //
      MonthState:"",//
      dialogShow:false,//Dialog
      Serveshow:false,
      username: '',
      password: '',
      TimeDaty:false,
      Timechoice:"",
    };
  },

  created() {
      this.Table = Tablejison;

      let clientHeight = document.documentElement.clientHeight;
      this.conter = Math.floor((clientHeight - 450) / 50);
      if(this.conter < 5){
        this.conter = 5
      }
      this.screenHeight = clientHeight - 370

      this.TotalPaging();

  },
  mounted() { 
    let a = document.getElementById('elem').offsetHeight
    let clientHeight = document.documentElement.clientHeight;
    this.conter = Math.floor((clientHeight - 450) / a);
    if(this.conter < 5){
        this.conter = 5
      }
    this.TotalPaging();
  },
  methods: {
    TotalPaging() {
      this.Selection = []
      this.Tablelist =[]
      this.Total = this.Table.length;
      this.pages = Math.ceil(this.Total / this.conter);
      for ( var i = (this.page - 1) * this.conter; i < this.page * this.conter;i++) {
        if(this.Table[i] != undefined){ 
          this.Table[i].mous = false;
            this.Tablelist.push(this.Table[i]);
        } 
      }
      console.log( this.Tablelist)
      this.Tablelist.map(item=>{ 
           if(item.state == "" || item.state == ""){
               item.state = ""
               item.tion = true;
          }else{
              item.state = ""
              item.tion = false;
          }
      })
    },
     enter(index){//
       this.Tablelist = [...this.Tablelist]
       this.Tablelist[index].mous = true
    },
    leave(){//
        this.Tablelist = [...this.Tablelist]
        this.Tablelist.map(item=>{
          item.mous = false
        })
    },
    Prev(){ //    
          console.log("")
          this.page --
          this.TotalPaging();
    },
    next(){//
          console.log("")
          this.page ++ 
          this.TotalPaging();
    },
    PlayerDetails(){ //
        this.$router.push({ path: "/ServerDetails"}); 
    },
    QuerySer(){ //
         
        //  ServeName:'', //  MonthState:"",// 
       console.log(this.ServeName,"1111111111",this.MonthState)
    },
    SerSwitch(open){

        console.log(open,"")
    },
    //
    outExe(){
     
          Dialog.confirm({
                  title: 'excel',
                  message: 'excel'
                }).then(() => {
                     this.Export2Excel()
                }).catch(() => {
                  // on cancel
                  });
    },
    Export2Excel() {
          var that = this;
          require.ensure([], () => {
              const { export_json_to_excel } = require('../../excel/Export2Excel'); //
              const tHeader = ['name','phone', 'state', 'Finish','Number','ID','tion','pid']; // 
              const filterVal = ['name','phone', 'state', 'Finish','Number','ID','tion','pid']; // 
              const list = Tablejison;
              const data = that.formatJson(filterVal, list);
              export_json_to_excel(tHeader, data, ``);// 
          })
    },
    formatJson(filterVal, jsonData) {
        return jsonData.map(v => filterVal.map(j => v[j]))
    },
    //
    AddServe(){
       this.dialogShow = true
    },
     beforeClose(action, done) {
      if (action === 'confirm') {
         let username = this.username
         let password = this.password
         console.log(username,password)
         if(username == password){
           this.Serveshow = true
         }else{
           alert("")
         }
          setTimeout(done, 100);
        
      } else {
        done();
      }
    },
    OpnTimeDaty(){
      this.TimeDaty = true
    },
    cancel(){
       this.TimeDaty = false
    },
    Sure(time){
        this.TimeDaty = false
        this.Timechoice = time
    },
    cancelSer(){
      this.Serveshow = false
    },
    SuerSer(){
      this.Serveshow = false
    }
  }
};
