import Axios from 'axios'
import Cookies from 'js-cookie'

Axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8'
Axios.defaults.headers.get['Content-Type'] = 'application/x-www-form-urlencoded'

//const baseURL='http://giggsgame.com'
const TOKEN_KEY='token'
class httpRequest {
    constructor() {
        this.options = {
            method: '',
            url:'',
            baseURL: ''
        }
        // 
        this.queue = {}
    }
    // 
    destory(url){
        delete this.queue[url]
        const queue=Object.keys(this.queue);
        return queue.length
    }
    //
    interceptors(instance){
        // url
        var data = JSON.parse(localStorage.getItem('token'));
        instance.interceptors.request.use(config=>{
            // tokentoken
            if(data){
                config.headers['Authorization']='Bearer '+ data //Cookie
            }
            return config
        },error=>{
            console.log(error,"@@@@@@@@@@@@@@@@@@")
            return Promise.reject(error)
        })
        // 
        instance.interceptors.response.use(res=>{
            let {data}=res
            //const is=this.destory(url)//
            if(res.status == 200 || res.status ==201 ||  res.status ==204){
                res.msg =""  
            }else{
                localStorage.removeItem('token');
                window.location.href=window.location.pathname+'#/'//
            }
            return data 
        },error=>{
             if(error.response.status == 500){
                alert("")
            }else{
                localStorage.removeItem('token');
                window.location.href=window.location.pathname+'#/'//        
            }
            return Promise.reject(error)
        })
    }
    //
    create(url){
        let conf={
            baseURL:url,
            timeout:8000,//
            ithCredentials:true//cookie
        }
        return Axios.create(conf)
    }
    //
    request(options){ 
        let instance=this.create(options.baseURL)//axios baseURL
        this.interceptors(instance,options.url)//axios
        options=Object.assign({},options)//axios
        return instance(options) //axios      
    }
}
export default httpRequest