export default {
    name:"Time",
     data() {  
       return {
                date: new Date(),
                YearList:["2019","2020","2021"],
                Year:2019,//
                Month:null,//
                Day:null,//
                Time:null,//
                Minute:null,//
                timestamp:null,
                 
       }
    },
    created(){
        this.mytime()

    },    
    mounted() {
      var _this = this;
      window.onresize = function(){ // 
        _this.screenHeight = document.documentElement.clientHeight -105
      };
        let MYtime = new Date();//date    
        let arr=["","","","","","",""]     
        _this.Month = MYtime.getMonth() + 1 
        _this.Day =  MYtime.getDate()
        _this.Time=MYtime.getHours()
        _this.Minute=MYtime.getMinutes()
        _this.date = MYtime
    },
     methods: {
        cancel(){
              this.$emit('cancel')  
          
        },
         mytime(){//
          let MYtime = new Date();//date    
          let Year = MYtime.getFullYear()   //
          let Month =  MYtime.getMonth() + 1  ;//
              Month =Month > 9 ?  Month :'0' + Month 
          let Day =   MYtime.getDate(); //
              Day =  Day  > 9 ?  Day :'0' +  Day ;

           let Time =   MYtime.getHours();//
               Time = Time  > 9 ?  Time :'0' +  Time ;

          let Minute =  MYtime.getMinutes() ; //
              Minute =Minute  > 9 ?  Minute :'0' +  Minute ;

          let times = `${Year}-${Month}-${Day} ${Time}:${Minute}:00 ` ;
          
          this.timestamp = new Date(times).getTime();
        
          
     },
        Sure(){
             let Month =   this.Month  > 9 ?  this.Month :'0' +  this.Month 
             let Day =   this.Day  > 9 ?  this.Day :'0' +  this.Day 
             
             let Time =   this.Time  > 9 ?  this.Time :'0' +  this.Time 
             let Minute =   this.Minute  > 9 ?  this.Minute :'0' +  this.Minute 
          
           //let  time  =` ${this.date.getFullYear()}-${this.Month}-${this.Day}  ${this.Time}:${this.Minute}`
            
            let time = `${this.Year}-${Month}-${Day} ${Time}:${Minute}:00 `
           
            let a = new Date(time).getTime();
            if(a < this.timestamp){
                alert("")
                return
            }else{
              this.$emit('Sure',time)
            }
            
        }
     }
}
