import Tablejison from "@/assets/json/Table.json";
import { Users }  from "@/api/api" ; 
import { Switch ,Dialog } from 'vant';
export default {
  name: "formTable",
  components:{Switch,Dialog},
  data() {  
    return {
      TableAcitve: false,
      screenHeight:null,
      Tbody: ["", "", "", "",  "ID","()","IP",""],
      Table: [],
      Tablelist: [],
      page: 1, //
      pages: null, //
      conter:10, //
      Total: null,//
      Selection:[] ,//
      UserName:"",//
      UserState:"",//
      dialogShow:false,
      dialogUsername:"",//
      dialogPassword:"",//
      Suershow:false,
      leaguer:null,                  
      GameType:"" //
    };
  },
  created() {
      let clientHeight = document.documentElement.clientHeight;
      this.conter = Math.floor((clientHeight - 450) / 53);
      if(this.conter < 5){
        this.conter = 5
      }
      this.screenHeight = clientHeight - 370
      this.User()
  },
  mounted() {},
  methods: {
     async User(){
             let res = await Users({
                page:this.page,
                limit:this.conter,
                name:this.UserName,
             })
              .then(res=>{ 
                 console.log(res.data)
                  this.Tablelist = res.data
                  this.Total = res.meta.total;
                  this.pages = res.meta.last_page        
              })
    },
    conetrModer(){
          if(this.page > this.pages ){
            this.page = this.pages
          }
          if(this.page < 1 ){
            this.page = 1
         }
    },
    TotalPaging() {
       
        this.User()
    },
    Prev(){ //    
          console.log("")
          this.page --
          this.User();
    },
    next(){//
          console.log("")
          this.page ++ 
          this.User();
    },
    AllSwitch(){//
        this.Tablelist = [...this.Tablelist]
        this.TableAcitve =! this.TableAcitve
        this.Selection = []
        
        if(this.TableAcitve){
            this.Tablelist.map(item=>{
               item.tion = true 
            })
            for(var i = 0;i< this.TableAcitve.length;i++){
                this.TableAcitve[i].dex = i
                this.Selection.push(item)
            }
            return
        }else{
           this.Tablelist.map(item=>{
               item.tion = false   
            }) 
        }
    },
    ToSwitch(index){//
           this.Tablelist = [...this.Tablelist]
           this.Tablelist[index].tion =! this.Tablelist[index].tion
            this.Selection = [] 
           this.Tablelist.map(item=>{
                if(item.tion){
                  item.dex = index
                  this.Selection.push(item)
                }
           })
           console.log( this.Selection)
    },
    PlayerDetails(item){
        this.$storage.setSession("UserDetails", item);
        this.$router.push({ path: "/UserDetails"}); 
    },
    enter(index){//
       this.Tablelist = [...this.Tablelist]
       this.Tablelist[index].mous = true
    },
    leave(){//
        this.Tablelist = [...this.Tablelist]
        this.Tablelist.map(item=>{
          item.mous = false
          
        })
    },
   
    //
    outExe(){
     
          Dialog.confirm({
                  title: '',
                  message: 'excel'
                }).then(() => {
                     this.Export2Excel()
                     alert("")
                }).catch(() => {
                     alert("")
                });
    },
    async Export2Excel() {
          var that = this;
          let res = await Users({
                page:1,
                limit: 99999,
             })
              .then(res=>{ 
                   
                require.ensure([], () => {
                   
                    const { export_json_to_excel } = require('../../excel/Export2Excel'); //
                    const list = res.data;
                    const tHeader = ["", "", "", "",  "ID","()","IP",""]; // 
                    const filterVal =  ['name','userid', 'mous', 'registertime','roomid','name','energe','energelimit']; // 
                 
                    const data = that.formatJson(filterVal, list);
                    export_json_to_excel(tHeader, data, ``);// 
                })    
             })
         
    },
    formatJson(filterVal, jsonData) {
        return jsonData.map(v => filterVal.map(j => v[j]))
    },
    //
    QuerySuer(){
        let UserName = this.UserName
        let UserState = this.UserState

    },
    SuerDelete(){
        console.log("")
    },
    AddSuer(){
        //this.Suershow = true
    },
     beforeClose(action, done) {
      if (action === 'confirm') {
         let dialogUsername = this.dialogUsername
         let dialogPassword = this.dialogPassword
      
         if(dialogUsername == dialogPassword){
           this.Suershow = true
         }else{
           alert("")
         }
          setTimeout(done, 100);
        
      } else {
        done();
      }
    },
   
     SuerSer(){
      this.Suershow = false
     },
      cancelSer(){
           this.Suershow = false
      },
  }
};
