import Xhead  from "@/components/Section/head.vue";
export default {
    name: "UserDetails",
    components:{Xhead},
    data() {  
       return {
               screenHeight: document.documentElement.clientHeight -85 ,//
                date: new Date(),
                getMonth:null ,// 
                getDay:null ,// 
                getSeconds:null,
                size:0,
                User:null,
                
       }
    },
    mounted() {
      var _this = this;
      window.onresize = function(){ // 
        _this.screenHeight = document.documentElement.clientHeight -85
      };
       this.timer = setInterval(function() {
                let MYtime = new Date();//date    
                let arr=["","","","","","",""]
                _this.getMonth = (MYtime.getMonth() + 1 ) > 9 ? (MYtime.getMonth() + 1):'0' + ( MYtime.getMonth() + 1 )
                 _this.getSeconds = MYtime.getSeconds()  > 9 ? MYtime.getSeconds() :'0' + MYtime.getSeconds()  
                _this.getDay =  arr[parseInt( MYtime.getDay() )]
                
                _this.date = MYtime
      }, 1000);

       this.drawLine();           
    },
    created() {
       this.User = this.$storage.getSession("UserDetails");
       console.log( this.User)
    },
    methods: {
        close(){
             this.$router.go(-1);
        },
        drawLine() {
             let MYtime = new Date();//date   
             let getMonth = (MYtime.getMonth() + 1 ) > 9 ? (MYtime.getMonth() + 1):'' + ( MYtime.getMonth() + 1 )
            let _this = this
            let myChart = this.$echarts.init(document.getElementById("myChart"));
            myChart.setOption({
                   title: {
                        text: ` ${getMonth}`
                    },
                    legend: {
                        
                        data:['','','','','']
                    },
                    tooltip : {
                        trigger: 'axis',
                         formatter: "{a}:{c}",
                        axisPointer: {
                            type: 'cross',
                            label: {
                                backgroundColor: '#6a7985'
                            }
                        }
                    },
                    xAxis: {
                        type: 'category',
                        data: [`${getMonth}1`, `${getMonth}2`,`${getMonth}3`,`${getMonth}4`,`${getMonth}5`,`${getMonth}6`,`${getMonth}7`,`${getMonth}8`,`${getMonth}9`,`${getMonth}10`,`${getMonth}11`,`${getMonth}12`,`${getMonth}13`]
                    },
                    yAxis: {
                        type: 'value',
                        axisLabel: {
                            formatter: '{value} '
                        }
                    },
                    series: [{
                        name:'',
                        data: [220, 832, 901, 334, 1290, 1330, 1320, 832, 901, 334, 1290, 1330, 1320],
                        type: 'line'
                    },{
                        name:'',
                        data: [320, 932, 901, 534, 1090, 1330, 1320, 932, 901, 534, 1090, 1330, 1320],
                        type: 'line'
                    },
                    {
                        name:'',
                        data: [420, 732, 901, 434, 90, 1330, 1320 ,732, 901, 434, 90, 1330, 1320],
                        type: 'line'
                    },{
                        name:'',
                        data: [520, 532, 901, 734, 190, 1330, 1320,532, 901, 734, 190, 1330, 1320],
                        type: 'line'
                    },{
                        name:'',
                        data: [820, 932, 901, 734, 1290, 1330, 1320, 932, 901, 734, 1290, 1330, 1320],
                        type: 'line'
                    }]
            })
        }
    }
}
