import { getRoles, getPermissions , postRoles , postPermissions , putRoles , putPermissions ,DeleteRoles , DeletePermissions} from "@/api/api";

export default {
  name: "AdminJurisdiction",
  components: {},
  data() {
    return {
      screenHeight: null,
      RolesList: null,
      permissionsList: null,
      fidexBox: false,
      boxUlShow: false,
      addType: 0, // 0= 1 == // 2= 3 ==
      EditID: 0, 
      keyIndex: null, //
      AdName: "",
      Title:"",
      Name:"",
    };
  },
  created() {
    let clientHeight = document.documentElement.clientHeight;
    //this.conter = Math.floor((clientHeight - 450) / 53);
    this.screenHeight = clientHeight - 370;
    this.Roles();
    this.Permissions();
  },
  mounted() {},
  methods: {
    async Roles() { //
      let res = await getRoles({}).then(res => {
        res.data.map(item => {
          item.Active = false;
        });

        this.RolesList = res.data;
        console.log(this.RolesList);
      });
    },
    async Permissions() { //
      let res = await getPermissions({}).then(res => {
        res.data.map(item => {
          item.Active = false;
        });
        this.permissionsList = res.data;
        console.log(this.permissionsList);
      });
    },
    async addPostRoles(){ //
        let a = []
        this.permissionsList.map(item => {
            if (item.Active) {
            a.push(item.name);
            }
        });
        if(a == ''){alert("")}
        let res =postRoles({
               name:this.Name,
               title:this.Title ,
               permissions:a,
        }).then(res=>{
            alert("")
            this.box()
            this.Roles()
        }).catch(reen=>{
            alert("")
        })
    },
    async addPermissions(){ //
            let res = await postPermissions({
                name:this.Name,
                title:this.Title ,
            }).then(res=>{
                alert("")
                this.Permissions()
                this.box()
            }).catch(reen=>{
                alert("")
            })
    } ,
    async EidtRoles(){ //
        let a = []
        this.permissionsList.map(item => {
            if (item.Active) {
               a.push(item.name);
            }
        });
        if(a == ''){alert("")}
        let res =putRoles({
               ID:this.EditID,
               name:this.Name,
               title:this.Title ,
               permissions:a,
        }).then(res=>{
            console.log(res.data)
             this.Roles()
            this.box()
             alert("")
        }).catch(reen=>{
            alert("")
        })
    }, 
    async EidtPermissions(){ //
            let res = await putPermissions({
                ID:this.EditID,
                name:this.Name,
                title:this.Title ,
            }).then(res=>{
               this.Permissions()
               this.box()
               alert("")
            }).catch(reen=>{
                alert("")
            })
    } ,
    async DeleRoles(item){ //
       let res = await DeleteRoles({
            ID:item.id
       }).then(res=>{
                this.Roles()
                this.box()
                alert("")
            }).catch(reen=>{
                alert("") 
            })
    },
    async DelePermissions(item){ //
           console.log(item)
            let res = await DeletePermissions({
                    ID:item.id
            }).then(res=>{
                        this.Permissions()
                        this.box()
                        alert("")
                    }).catch(reen=>{
                        alert("")
                    })
    },
    SubFrom(){
        if(this.Title == '' || this.Name == ''){
            alert('(2)')
            return
        }
       if(this.addType == 0){ //
          this.addPostRoles()
          return
       }
       if(this.addType == 1){ //
       this.addPermissions()
         return
       }
       if(this.addType == 2){ //
          this.EidtRoles()
          return
       }
       if(this.addType == 3){ //
         this.EidtPermissions()
          return
       }
    },
    box(){
        this.permissionsList = [...this.permissionsList];
        this.Title ="",
        this.Name = "",
        this.permissionsList.map(item => {
          item.Active = false;
        });
        this.fidexBox = false
        this.boxUlShow = false
    },
    addList(num) {
     
      this.addType = num;
      this.fidexBox = true;
      if (num == 0) {
        this.AdName = "";
      } else {
        this.AdName = "";
      }
    },
    Edit(num,item,index) {
        this.EditID = item.id;
        this.fidexBox = true;
        this.addType = num;
        this.keyIndex = index;
      if (num == 2) {
        this.AdName = "";
        this.Name = item.name
        this.Title = item.title
         this.permissionsList = [...this.permissionsList];
        item.permissions.map (res=>{
             this.permissionsList.map(item =>{
                if(item.name ==  res.name){
                    item.Active = true
                }
             })
        })
      }else {
        this.AdName = "";
        this.Name = item.name
        this.Title = item.title
      }
      
    },
    boxSwich(item, index, num) {
        //
        this.permissionsList = [...this.permissionsList];
        this.permissionsList[index].Active = !this.permissionsList[index].Active;
      
    },
    cancel(){
        this.fidexBox = false
        this.boxUlShow = false 
    },
    perationBoxUlShow(){
       this.boxUlShow = true
    },
    NoperationBoxUlShow() {
        this.boxUlShow = false
    }
  }
};
