import Tablejison from "@/assets/json/Table.json"; 
export default {
  name: "NinBlacklist",
  components:{},
  data() {  
    return {
      TableAcitve: false,
      screenHeight:null,
      Tbody: ["","", "ID", "","","", "","",  ""],
      Table: [],
      Tablelist: [],
      page: 1, //
      pages: null, //
      conter: 10, //
      Total: null,//
      Selection:[] ,//
      ForbiddenWords:false,
      PlayName:"",
      PlayID:"",
      PlayServe:"",
      PlayerList:[],
    };
  },
  created() {
      this.Table = Tablejison;
      let clientHeight = document.documentElement.clientHeight;
      this.conter = Math.floor((clientHeight - 450) / 50);
      if(this.conter < 5){
        this.conter = 5
      }
      this.screenHeight = clientHeight - 370
      this.TotalPaging();
  },
  mounted() {},
  methods: {
    TotalPaging() {
      this.Selection = []
      this.Tablelist =[]
      this.Total = this.Table.length;
      this.pages = Math.ceil(this.Total / this.conter);
      for ( var i = (this.page - 1) * this.conter; i < this.page * this.conter;i++) {
        if(this.Table[i] != undefined){ 
          this.Table[i].tion = false;
          if(this.Table[i].state == "" || this.Table[i].state == "" ){
            this.Table[i].Finish = ""
          }else{
             this.Table[i].Finish = ""
          }
            this.Tablelist.push(this.Table[i]);
        } 
      }
    },
    Prev(){ //    
          console.log("")
          this.page --
          this.TotalPaging();
    },
    next(){//
          console.log("")
          this.page ++ 
          this.TotalPaging();
    },
    enter(index){//
        this.Tablelist = [...this.Tablelist]
        this.Tablelist[index].mous = true
      },
      leave(){//
          this.Tablelist = [...this.Tablelist]
          this.Tablelist.map(item=>{
            item.mous = false
          })
      },
  

    
   
    NinBlack(index){
        this.ForbiddenWords = true;
    },
     AddPlayerBlack(){
      this.ForbiddenWords = false;
    },
    NoForbiddenWords(){
        this.ForbiddenWords = false;
    },
    PlayerName(){//
          let a = []
          let PlayName = this.PlayName
          if(PlayName ==''){
            this.PlayerList = []
            return
          }
         
          this.Table.map(item=>{
             if(item.name.indexOf(`${PlayName}`) != -1){
               a.push(item)
             }
          })
         this.PlayerList = a
    },
    PlayerID(){
          let a = []
          let PlayID = this.PlayID
          if(PlayID ==''){
              this.PlayerList = []
            return
          }
          this.Table.map(item=>{
             if(item.ID.indexOf(`${PlayID}`) != -1){
               a.push(item)
             }
          })
         this.PlayerList = a
    },
    PlayerServe(){
          let a = []
          let PlayServe = this.PlayServe
          if(PlayServe ==''){
            this.PlayerList = []
             return
          }
          this.Table.map(item=>{
             if(item.phone.indexOf(`${PlayServe}`) != -1){
               a.push(item)
             }
          })
         this.PlayerList = a
    },
    ChoicePlay(item){
      this.PlayName = item.name
      this.PlayID = item.ID
      this.PlayServe = item.phone
       this.PlayerList = []
    },
   
  }
};
